import { deepEqual } from "./base";

export default {
  install: function (Vue) {
    Vue.prototype.$debounce = function (fn, delay) {
      let timer = null; //
      return function () {
        if (timer) {
          clearTimeout(timer); //
          timer = setTimeout(fn, delay);
        } else {
          timer = setTimeout(fn, delay); // 
        }
      };
    };

    Vue.prototype.$percentage = function (ratio, digits, isSymbol) {
      if (ratio == null || ratio == undefined) return "--";

      digits = digits || 0;

      let symbol = "";

      if (isSymbol && ratio) {
        symbol = ratio < 0 ? "-" : "+";
      }

      const pnum = parseFloat((100 * ratio).toFixed(digits));

      return `${symbol}${pnum}`;
    };

    Vue.prototype.$deepEquals = deepEqual;
  },
};
