/**  */
export const noop = () => {
  /** noop */
};

/** tag */
export function getTag(value) {
  if (value == null) {
    return value === undefined ? "[object Undefined]" : "[object Null]";
  }
  return toString.call(value);
}

// Boolean
export const isBoolean = (o) => getTag(o) === "[object Boolean]";

// null
export const isNull = (o) => getTag(o) === "[object Null]";

// undefined
export const isUndefined = (o) => getTag(o) === "[object Undefined]";

// null or undefined
export const isNil = (o) => isNull(o) || isUndefined(o);

// 
export const isObject = (fn) => !isNil(fn) && typeof fn === "object";

export function deepEqual(obj1, obj2) {
  if (obj1 === obj2) return true;
  if (!obj1 || !obj2) return false;

  const keys1 = Object.keys(obj1);
  const keys2 = Object.keys(obj2);

  if (keys1.length !== keys2.length) {
    return false;
  }

  for (const key of keys1) {
    const val1 = obj1[key];
    const val2 = obj2[key];
    const areObjects = isObject(val1) && isObject(val2);
    if (
      (areObjects && !deepEqual(val1, val2)) ||
      (!areObjects && val1 !== val2)
    ) {
      return false;
    }
  }

  return true;
}
