<template>
  <div
    ref="myChart"
    :class="className"
    :style="{ height: height, width: width }"
  />
</template>

<script>
import * as echarts from "echarts";

export default {
  props: {
    className: {
      type: String,
      default: "container",
    },
    width: {
      type: String,
      default: "100%",
    },
    height: {
      type: String,
      default: "300px",
    },
    chartScale: {
      type: Number,
    },
    chartOptions: {
      type: Object,
    },
    data: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  data() {
    return {
      chart: null,
      fontSize: 14,
      scale: 1,
      optionAll: 0,
    };
  },
  watch: {
    data: {
      handler(cur, old) {
        if (this.$deepEquals(cur, old)) return;

        this.initChart();
      },
      deep: true,
    },
  },
  mounted() {
    this.initChart();
    window.addEventListener("resize", () => {
      this.initChart();
    });
  },
  methods: {
    initChart() {
      let that = this;
      that.scale =
        this.chartScale || document.documentElement.clientWidth / 1080;
      const myChart = echarts.init(this.$refs.myChart);
      const data = this.data;

      for (let item of data) {
        that.optionAll = this.optionAll + item.value;
      }

      //  series-surface.parametricEquation
      function getParametricEquation(
        startRatio,
        endRatio,
        isSelected,
        isHovered,
        k,
        h
      ) {
        // 
        let midRatio = (startRatio + endRatio) / 2;

        let startRadian = startRatio * Math.PI * 2;
        let endRadian = endRatio * Math.PI * 2;
        let midRadian = midRatio * Math.PI * 2;

        // 
        if (startRatio === 0 && endRatio === 1) {
          isSelected = false;
        }

        // / k 1/3
        k = typeof k !== "undefined" ? k : 1 / 3;

        //  x y  0
        let offsetX = isSelected ? Math.cos(midRadian) * 0.1 : 0;
        let offsetY = isSelected ? Math.sin(midRadian) * 0.1 : 0;

        //  1
        let hoverRate = isHovered ? 1.05 : 1;

        // 
        return {
          u: {
            min: -Math.PI,
            max: Math.PI * 3,
            step: Math.PI / 32,
          },

          v: {
            min: 0,
            max: Math.PI * 2,
            step: Math.PI / 20,
          },

          x: function (u, v) {
            if (u < startRadian) {
              return (
                offsetX +
                Math.cos(startRadian) * (1 + Math.cos(v) * k) * hoverRate
              );
            }
            if (u > endRadian) {
              return (
                offsetX +
                Math.cos(endRadian) * (1 + Math.cos(v) * k) * hoverRate
              );
            }
            return offsetX + Math.cos(u) * (1 + Math.cos(v) * k) * hoverRate;
          },

          y: function (u, v) {
            if (u < startRadian) {
              return (
                offsetY +
                Math.sin(startRadian) * (1 + Math.cos(v) * k) * hoverRate
              );
            }
            if (u > endRadian) {
              return (
                offsetY +
                Math.sin(endRadian) * (1 + Math.cos(v) * k) * hoverRate
              );
            }
            return offsetY + Math.sin(u) * (1 + Math.cos(v) * k) * hoverRate;
          },

          z: function (u, v) {
            if (u < -Math.PI * 0.5) {
              return Math.sin(u);
            }
            if (u > Math.PI * 2.5) {
              return Math.sin(u) * h * 0.1;
            }
            return Math.sin(v) > 0 ? 1 * h * 0.1 : -1;
          },
        };
      }

      //  3D 
      function getPie3D(pieData, internalDiameterRatio) {
        let series = [];
        let sumValue = 0;
        let startValue = 0;
        let endValue = 0;
        let legendData = [];
        let k =
          typeof internalDiameterRatio !== "undefined"
            ? (1 - internalDiameterRatio) / (1 + internalDiameterRatio)
            : 1 / 3;

        //  series-surface 
        for (let i = 0; i < pieData.length; i++) {
          sumValue += pieData[i].value;

          let seriesItem = {
            name:
              typeof pieData[i].name === "undefined"
                ? `series${i}`
                : pieData[i].name,
            type: "surface",
            parametric: true,
            wireframe: {
              show: false,
            },
            pieData: pieData[i],
            pieStatus: {
              selected: false,
              hovered: false,
              k: k,
            },
          };

          if (typeof pieData[i].itemStyle != "undefined") {
            let itemStyle = {};

            typeof pieData[i].itemStyle.color != "undefined"
              ? (itemStyle.color = pieData[i].itemStyle.color)
              : null;
            typeof pieData[i].itemStyle.opacity != "undefined"
              ? (itemStyle.opacity = pieData[i].itemStyle.opacity)
              : null;

            seriesItem.itemStyle = itemStyle;
          }
          series.push(seriesItem);
        }

        //  sumValue getParametricEquation 
        //  series-surface  series-surface.parametricEquation
        for (let i = 0; i < series.length; i++) {
          endValue = startValue + series[i].pieData.value;

          series[i].pieData.startRatio = startValue / sumValue;
          series[i].pieData.endRatio = endValue / sumValue;
          series[i].parametricEquation = getParametricEquation(
            series[i].pieData.startRatio,
            series[i].pieData.endRatio,
            false,
            false,
            k,
            series[i].pieData.value
          );

          startValue = endValue;

          legendData.push(series[i].name);
        }

        // 
        series.push({
          name: "mouseoutSeries",
          type: "pie",
          tooltip: {
            show: false,
          },
          label: {
            alignTo: "edge",
            show: true,
            position: "outside",
            minMargin: 5,
            edgeDistance: 10,
            lineHeight: 20,
            formatter: "{b|{b}} \n {c|{c}}",
            rich: {
              b: { fontSize: 14, color: "#fff" },
              c: { fontSize: 14, color: "#33ccff" },
            },
          },
          labelLine: {
            length: 10,
            length2: 0,
          },
          radius: ["75%", "75%"],
          center: ["50%", "50%"],
          parametric: false,
          wireframe: { show: false },
          itemStyle: {
            opacity: 1,
          },
          data: data,
          parametricEquation: {
            u: { min: 0, max: Math.PI * 2, step: Math.PI / 20 },
            v: { min: 0, max: Math.PI, step: Math.PI / 20 },
            x: function (u, v) {
              return Math.sin(v) * Math.sin(u) + Math.sin(u);
            },
            y: function (u, v) {
              return Math.sin(v) * Math.cos(u) + Math.cos(u);
            },
            z: function (u, v) {
              return Math.cos(v) > 0 ? 0.1 : -0.1;
            },
          },
          ...that.chartOptions.series,
        });

        //  legendDataseries 
        let option = {
          tooltip: {
            formatter: (params) => {
              if (params.seriesName !== "mouseoutSeries") {
                return `${params.seriesName} ${(
                  (option.series[params.seriesIndex].pieData.value /
                    that.optionAll) *
                  100
                ).toFixed(2)} %`;
              }
            },
          },
          xAxis3D: { min: -1, max: 1 },
          yAxis3D: { min: -1, max: 1 },
          zAxis3D: { min: -1, max: 1 },
          grid3D: {
            show: false,
            boxHeight: 10,
            viewControl: {
              //3d
              alpha: 30,
              rotateSensitivity: 0,
              zoomSensitivity: 0,
              panSensitivity: 0,
              autoRotate: false,
            },
            //SSAO
            postEffect: {
              //
              enable: true,
              bloom: {
                enable: true,
                bloomIntensity: 0.1,
              },
              SSAO: {
                enable: true,
                quality: "medium",
                radius: 2,
              },
            },
          },
          series: series,
          ...that.chartOptions.option,
        };
        return option;
      }

      //  option
      let option = getPie3D(data, 0.59);
      myChart.setOption(option);
      // 
      let hoveredIndex = "";
      //  mouseover
      myChart.on("mouseover", function (params) {
        // 
        let isSelected;
        let isHovered;
        let startRatio;
        let endRatio;
        let k;

        //  mouseover 
        if (hoveredIndex === params.seriesIndex) {
          return;

          // 
        } else {
          //  option 
          if (hoveredIndex !== "") {
            //  option.series  false
            isSelected = option.series[hoveredIndex].pieStatus.selected;
            isHovered = false;
            startRatio = option.series[hoveredIndex].pieData.startRatio;
            endRatio = option.series[hoveredIndex].pieData.endRatio;
            k = option.series[hoveredIndex].pieStatus.k;

            //  option 
            option.series[hoveredIndex].parametricEquation =
              getParametricEquation(
                startRatio,
                endRatio,
                isSelected,
                isHovered,
                k,
                option.series[hoveredIndex].pieData.value
              );
            option.series[hoveredIndex].pieStatus.hovered = isHovered;

            //  seriesIndex 
            hoveredIndex = "";
          }

          //  mouseover  option 
          if (params.seriesName !== "mouseoutSeries") {
            //  option.series  true
            isSelected = option.series[params.seriesIndex].pieStatus.selected;
            isHovered = true;
            startRatio = option.series[params.seriesIndex].pieData.startRatio;
            endRatio = option.series[params.seriesIndex].pieData.endRatio;
            k = option.series[params.seriesIndex].pieStatus.k;

            //  option 
            option.series[params.seriesIndex].parametricEquation =
              getParametricEquation(
                startRatio,
                endRatio,
                isSelected,
                isHovered,
                k,
                option.series[params.seriesIndex].pieData.value + 5
              );
            option.series[params.seriesIndex].pieStatus.hovered = isHovered;

            //  seriesIndex
            hoveredIndex = params.seriesIndex;
          }

          //  option
          myChart.setOption(option);
        }
      });
    },
  },
};
</script>
<style scoped lang="less">
.highcharts-tooltip {
  font-size: 37px;
}
</style>
